import React, { Component } from 'react';
import gql from 'graphql-tag';
import { Query } from 'react-apollo';
import classNames from 'classnames';

const LAUNCH_QUERY = gql`
    query LaunchQuery($flight_number: Int!) {
        launch(flight_number: $flight_number) {
            flight_number
            mission_name
            launch_year
            launch_success
            launch_date_local
            details
            upcoming
            rocket {
                rocket_name
                rocket_type
            }
            timeline {
                ignition
                liftoff
                maxq
                meco
                stage_sep
                second_stage_ignition
                webcast_liftoff
            }
        }
    }
`;

export class Launch extends Component {
    render() {
        let { flight_number } = this.props.match.params
        flight_number = parseInt(flight_number);
        return (
            <>
                <Query query={LAUNCH_QUERY} variables={{ flight_number }}>
                    {
                        ({ loading, error, data }) => {
                            if (loading) return <h4>Loading...</h4>
                            if (error) console.log(error);

                            const { mission_name, flight_number, launch_year, launch_success, details, upcoming,
                                rocket: { rocket_name, rocket_type },
                            } = data.launch;

                            return (
                                <div>
                                    <h1 className="display-4 my-3">{mission_name}</h1>
                                    <h4 className="my-3">Launch Details</h4>
                                    <ul className="list-group">
                                        {upcoming ? (

                                            <li className="list-group-item">
                                                <span className='text-warning'>Upcoming Launch!</span>
                                            </li>
                                        ) : (
                                                <li className="list-group-item">
                                                    <span className={classNames({
                                                        'text-success': launch_success,
                                                        'text-danger': !launch_success
                                                    })}>
                                                        {launch_success ? 'Launch Succeeded' : 'Launch Failed'}
                                                    </span>
                                                </li>
                                            )}
                                        <li className="list-group-item">
                                            Flight Number: {flight_number}
                                        </li>
                                        <li className="list-group-item">
                                            Launch Year: {launch_year}
                                        </li>
                                        {details ? (
                                            <li className="list-group-item">
                                                {details}
                                            </li>
                                        ) : (
                                                <></>
                                            )}
                                    </ul>
                                    <h4 className="my-3">Rocket Details</h4>
                                    <ul className="list-group">
                                        <li className="list-group-item">
                                            Rocket Name: {rocket_name}
                                        </li>
                                        <li className="list-group-item">
                                            Rocket Type: {rocket_type}
                                        </li>
                                    </ul>
                                    <div>
                                        {data.launch.timeline ? (
                                            <div>
                                                <h4 className="my-3">Timeline</h4>
                                                <ul className="list-group">
                                                    <li className="list-group-item">
                                                        Ignition: {data.launch.timeline.ignition ? data.launch.timeline.ignition : 'N/A'}
                                                    </li>
                                                    <li className="list-group-item">
                                                        Liftoff: {data.launch.timeline.liftoff != null ? data.launch.timeline.liftoff : 'N/A'}
                                                    </li>
                                                    <li className="list-group-item">
                                                        Max Q: {data.launch.timeline.maxq ? data.launch.timeline.maxq : 'N/A'}
                                                    </li>
                                                    <li className="list-group-item">
                                                        Main Engine Cutoff: {data.launch.timeline.meco ? data.launch.timeline.meco : 'N/A'}
                                                    </li>
                                                    <li className="list-group-item">
                                                        Stage Separation: {data.launch.timeline.stage_sep ? data.launch.timeline.stage_sep : 'N/A'}
                                                    </li>
                                                    <li className="list-group-item">
                                                        Second Stage Ignition: {data.launch.timeline.second_stage_ignition ? data.launch.timeline.second_stage_ignition : 'N/A'}
                                                    </li>
                                                    <li className="list-group-item">
                                                        Liftoff (webcast): {data.launch.timeline.webcast_liftoff ? data.launch.timeline.webcast_liftoff : 'N/A'}
                                                    </li>
                                                </ul>
                                            </div>
                                        ) : (
                                                <></>
                                            )}
                                    </div>
                                    <hr />
                                </div>
                            );
                        }
                    }
                </Query>
            </>
        );
    }
}

export default Launch
